.home__container {
    row-gap: 7rem;
}

.home__content { 
    grid-template-columns: 116px repeat(2, 1fr);
    padding-top: 5.5rem;
    column-gap: 2rem;
    align-items: center;
}

.home__social {
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;
}

.home__social-icon {
    font-size: 1.25rem;
    color: var(--title-color);
}

.home__social-icon:hover {
    color: var(--title-color-dark);
}

.home__title {
    /* font-size: var(--big-font-size); */
    font-size: 2.5rem;
    margin-bottom: var(--mb-0-75);
}

.home__subtitle {
    text-align: center;
    position: relative;
    font-size: var(--h3-font-size);
    /* padding-left: 5.4rem;
    padding-right: 5.4rem; */
    font-weight: var(--font-norm);
    margin-bottom: var(--bm-1);
}

.home__subtitle::before {
    content: ''; 
    position: absolute;
    width: 40px;
    height: 1px;
    background-color: var(--text-color);
    left: 0;
    top: 1rem;
}
.home__subtitle::after {
    content: ''; 
    position: absolute;
    width: 40px;
    height: 1px;
    background-color: var(--text-color);
    right: 0;
    top: 1rem;
}


.home__hand {
    width: 38px;
    height: 38px;
    margin-left: 2rem;
}

.home__description {
    max-width: 800px;
    margin-bottom: var(--mb-3);
}

.home__img {
    background: url("../../assets//S__2146313_0.jpg");
    /* background: url("../../assets//me.JPG");  */
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: insert 0 0 0 9px rgb(255 255 255 / 30%);
    order: 1;
    justify-self: center;
    width: 300px;
    height: 300px;
    animation: profile__animate 8s ease-in-out infinite 1s;
}

@keyframes profile__animate {
    0% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }

    50% {
        border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    }

    100% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
}


.home__scroll {
    margin-left: 9.25rem;
}

.wheel {
    animation: scroll 2s ease infinite;
}

@keyframes scroll {
    0% {
        transform: translateY(0);
    }

    30% {
        transform: translateY(3.75rem);
    }

}

.home__scroll-name {
    color: var(--title-color); 
    font-weight: var(--font-medium);
    margin-right: var(--mb-0-25);
    margin-left: 2px;
}

.homr__scroll-arrow {
    font-size: 1.25rem;
    color: var(--title-color);
}


/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
    .home__content {
        grid-template-columns: 100px repeat(2, 1fr);
        column-gap: 1.25rem;
    }

    .home__hand {
        width: 26px;
        height: 26px;
        margin-left: 1rem;
    }

    .home__subtitle {
        padding-left: 3.75rem;
        padding-right: 3.75rem;
        margin-bottom: var(--mb1);
    }

    .home__subtitle::before {
        width: 22px;
        top: 0.8rem;
    }

    .home__subtitle::after {
        width: 22px;
        top: 0.8rem;
    }

    .home__description {
        max-width: initial;
        margin-bottom: var(--mb-2-5);
    }

    .home__img {
        width: 250px;
        height: 250px;
        box-shadow: insert 0 0 0 8px rgb(255 255 255 / 30%);
    }

    .home__scroll {
        margin-left: 7.5rem;
    }

}

/* For medium devices */
@media screen and (max-width: 768px) {
    .home__content {
        grid-template-columns: 0.5fr 3fr;
        padding-top: 3.5rem;
    }

    .home__img {
        order: initial;
        justify-content: initial;
    }

    .home__data {
        grid-column: 1/3;
    }

    .home__warp_button {
        text-align: center;
    }

    .home__img {
        width: 280px;
        height: 280px;
        margin-right: 2rem;
        box-shadow: insert 0 0 0 6px rgb(255 255 255 / 30%);
    }

    .home__title {
        /* font-size: var(--big-font-size); */
        text-align: center;
        font-size: 2rem;
        margin-bottom: var(--mb-0-75);
    }

    .home__scroll { 
        display: none;
    }

    .home__subtitle {
        z-index: -1;
    }

    .home__subtitle::before {
        width: 25%;
        top: 0.8rem;
    }

    .home__subtitle::after {
        width: 25%;
        top: 0.8rem;
    }
}



/* For small devices */
@media screen and (max-width: 350px) {
    .home__content {
        padding: 0;
        margin: 0;
    }
    .home__ing {
        width: 180px;
        height: 180px;
    }

    .home__hand {
        width: 22px;
        height: 22px;
        /* margin-left: 1rem; */
    }

    .home__social {
        display: none;
    }

}